export default {
	data() {
		return {
			pass: '',
			passIcon: false,
			password: true,
			pass1: '',
			passIcon1: false,
			password1: true,
			pass2: '',
			passIcon2: false,
			password2: true
		}
	},
	methods: {
		tapPass: function(e) {
			this.pass = e.target.value;
			if (e.target.value.length > 0) {
				this.passIcon = true;
			} else {
				this.passIcon = false;
			}
		},
		clearPassIcon: function() {
			this.pass = '';
			this.passIcon = false;
		},
		tapSee: function(e) {
			this.password = !this.password;
		},
		tapPass1: function(e) {
			this.pass1 = e.target.value;
			if (e.target.value.length > 0) {
				this.passIcon1 = true;
			} else {
				this.passIcon1 = false;
			}
		},
		clearPassIcon1: function() {
			this.pass1 = '';
			this.passIcon1 = false;
		},
		tapSee1: function(e) {
			this.password1 = !this.password1;
		},
		tapPass2: function(e) {
			this.pass2 = e.target.value;
			if (e.target.value.length > 0) {
				this.passIcon2 = true;
			} else {
				this.passIcon2 = false;
			}
		},
		clearPassIcon2: function() {
			this.pass2 = '';
			this.passIcon2 = false;
		},
		tapSee2: function(e) {
			this.password2 = !this.password2;
		},
		formSubmit: function(e) {
			if(this.pass.length < 1){
				uni.showToast({
					icon:'none',
					title: '请输入旧密码',
					duration: 1000
				});
			}else if(this.pass1.length < 1){
				uni.showToast({
					icon:'none',
					title: '请输入新密码',
					duration: 1000
				});
			}else if (this.pass2.length < 1) {
				uni.showToast({
					icon: 'none',
					title: '请确认密码',
					duration: 1000
				});
			}else if (this.pass1 != this.pass2) {
				uni.showToast({
					icon: 'none',
					title: '两次新密码不一样',
					duration: 1000
				});
			}else{
				uni.request({
					url: this.$Url + '/api/v1/reset', //仅为示例，并非真实接口地址。
					method: 'POST',
					data: {
						token: uni.getStorageSync('token'),
						oldpassword:this.pass,
						newpassword:this.pass2
					},
					header: {
						'content-type': 'application/x-www-form-urlencoded'
					},
					success: (res) => {
						if (res.data.code == 200) {
							uni.showToast({
								title: res.data.msg,
								duration: 1000
							});
							uni.setStorageSync('userData', '');
							uni.setStorageSync('logon_status', 0);
							uni.setStorageSync('token', 0);
							setTimeout(() => {
								// uni.switchTab({
								//     url: '../../tabBar/my/my'
								// });
								uni.reLaunch({
								    url: '../../login/login'
								});
							},1000,)
						} else {
							uni.showToast({
								icon: 'none',
								title: '网络不给力，请稍后重试',
								duration: 1000
							});
						}
					}
				});
			}
		}
	}
}
